header {
    height: 70px;
    margin: 0 auto;
    box-shadow: 0px 3px 6px #00000029;
    @media(max-width: 1095px) {
        width: 100vw;
    } 
}

.header-container {
    height: 60px;
    display: flex;
    justify-content: space-between;


    .logo {
        @include logo();
    }
}